import styled from "styled-components";

import {
	Content, Item, Portal, Root, SubContent,
} from "@radix-ui/react-dropdown-menu";

import {
	colors, displayFontSizes, spacingMap, widthSizes,
} from "../styleguide";
import { pxToRem } from "../utils/pxToRem";

import { StyledDropDownMenuItemProps, StyledDropDownMenuProps } from "./DropDown.types";

export const StyledDropDownRoot = styled(Root)`
	position: relative;
`;

export const StyledDropDownMenuPortal = styled(Portal) <StyledDropDownMenuProps>`
	position: absolute;
	background:  ${colors.white};
	width: ${({ $menuWidth }) => $menuWidth || pxToRem(300)};
	transition: opacity .1s cubic-bezier(.215, .61, .355, 1);
`;

export const StyledDropDownMenuItem = styled(Item) <StyledDropDownMenuItemProps>`
	&:hover {
		${({ $isDisabled, color }) => !$isDisabled && `
			color: ${color ? (colors[color as keyof typeof colors] || color) : colors.midBlue};
			background: ${colors.midBlue}1A;
		`}
		cursor: ${({ $isDisabled }) => ($isDisabled ? "not-allowed" : "pointer")};
	}

	&:focus-within, &:active, &:focus-visible {
		${({ $isDisabled, color }) => !$isDisabled && `
			color: ${color ? (colors[color as keyof typeof colors] || color) : colors.midBlue};
			background: ${colors.midBlue}1A;
			outline: none;
		`}
	}

	color: ${({ $isSelected, color }) => {
		if ($isSelected) return colors.midBlue;
		return color ? (colors[color as keyof typeof colors] || color) : colors.black;
	}};

	background: ${({ $isSelected }) => {
		if ($isSelected) return `${colors.midBlue}1A`;
		return colors.white;
	}};

	font-family: ${displayFontSizes.bodyCopy.$fontFamily};
	font-size:  ${displayFontSizes.bodyCopy.$fontSize};

	font-weight: ${({ $isSelected }) => {
		if ($isSelected) return displayFontSizes.bodyCopyBold.$fontWeight;
		return displayFontSizes.bodyCopy.$fontWeight;
	}};
	list-style: none;
	padding: ${spacingMap.xxs} ${pxToRem(12)};
	gap: ${pxToRem(10)};
	height: ${pxToRem(28)};
	line-height: ${displayFontSizes.bodyCopy.lineHeight};
	display: flex;
	align-items: center;
	width: 100%;

	${({ $isDisabled }) => $isDisabled && `
	cursor: "not-allowed";
	color: ${colors.defaultGray};
	background: ${colors.white};`}

`;

export const StyledDropDownContent = styled(Content) <StyledDropDownMenuProps>`
	overflow-x: hidden;
	display: flex;
	margin: initial;
	flex-direction: column;
	justify-content: center;
	background: ${colors.white};

	align-items: flex-start;
	padding: 0 0 ${pxToRem(10)};
	gap: ${pxToRem(10)};
	position: relative;
	min-width: fit-content;
	height:  ${({ $menuHeight }) => $menuHeight || widthSizes.sr};

	overflow-y: scroll;
	::-webkit-scrollbar{
		display: none;
	}
	width: ${({ $menuWidth }) => $menuWidth || pxToRem(300)};

	box-shadow: 0 ${spacingMap.lg} ${spacingMap.lg} -8px ${colors.black}24,
	0 ${spacingMap.sm} ${spacingMap.sm} ${colors.black}24, 0 ${spacingMap.xs} ${spacingMap.xs} ${colors.black}24,
	0 ${spacingMap.xxs} ${spacingMap.xxs} ${colors.black}24, 0 2px 2px ${colors.black}24;
	border-radius: 8px;
	z-index: ${({ $zIndex }) => $zIndex || 1};
`;

export const StyledItemWrapper = styled.div<StyledDropDownMenuProps>`
	height:  ${({ $menuHeight }) => $menuHeight || widthSizes.sr};
	width: 100%;
`;

export const StyledDropDownSubContent = styled(SubContent) <StyledDropDownMenuProps>`
	overflow-x: hidden;
	display: flex;
	margin: initial;
	flex-direction: column;
	justify-content: center;
	background: ${colors.white};

	align-items: flex-start;
	padding: 0 0 ${pxToRem(10)};
	gap: ${pxToRem(10)};
	position: relative;
	min-width: fit-content;
	height:  ${({ $menuHeight }) => $menuHeight || "fit-content"};

	overflow-y: scroll;
	::-webkit-scrollbar{
		display: none;
	}
	width: ${({ $menuWidth }) => $menuWidth || "fit-content"};

	box-shadow: 0 ${spacingMap.lg} ${spacingMap.lg} -8px ${colors.black}24,
	0 ${spacingMap.sm} ${spacingMap.sm} ${colors.black}24, 0 ${spacingMap.xs} ${spacingMap.xs} ${colors.black}24,
	0 ${spacingMap.xxs} ${spacingMap.xxs} ${colors.black}24, 0 2px 2px ${colors.black}24;
	border-radius: 8px;
`;
